import {NgModule} from '@angular/core'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'

import {
  UserMainComponent,
  UserPanelComponent,
  UserRoutes
} from './'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    UserRoutes
  ],
  declarations: [
    UserMainComponent,
    UserPanelComponent
  ]
})
export class UserModule {
  public static routes = UserRoutes
}



